//removing the autofill styles
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
textarea:-webkit-autofill:active {
    // transition: background-color 5000s ease-in-out 0s;
    -webkit-transition: "color 9999s ease-out, background-color 9999s ease-out";
    -webkit-transition-delay: 9999s;
    transition: color 9999s ease-out, background-color 9999s ease-out;
    transition-delay: 9999s;
}

// remove autofill for edge
input.edge-autofilled,
textarea.edge-autofilled {
    background: none !important;
}

input[type="text"],
input[type="search"],
input[type="email"] {
    display: inline-block;
    font-size: 18px;
    line-height: 48px;
    color: $color-black;
    background: $color-white;
    width: 100%;
    height: 48px;
    padding: 15px 20px;
    -webkit-appearance: none;
    border: none;
    border-radius: 0;

    @include respond-to(tablet) {
        height: 40px;
        line-height: 40px;
    }


    &:focus {
        outline: none;
    }
}

.formfield {
    margin-bottom: 28px;

    &--submit {
        display: flex;
        flex-wrap: wrap;

        >*:not(:last-child) {
            margin-right: 32px;
        }
    }
}

label {
    display: block;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 8px;

    &.is-required {
        &::after {
            content: "*";
            color: $color-orange;
            font-size: 120%;
            line-height: 80%;
        }
    }
}

input:not([type="submit"]) {
    width: 100%;
    height: 47px;
    padding: 10px 24px;
    border: 1px solid $color-black-light;
    font-size: 18px;
    border-radius: 4px;

    &::placeholder {
        opacity: 1;
    }
}

.forgot-password {
    display: block;
    font-size: 14px;
    font-weight: 500;
    text-decoration: underline;

    &:hover {
        color: $color-purple;
    }
}

.formfield+.forgot-password {
    margin-top: -20px;
    margin-bottom: 28px;
}

select {
    min-width: 260px;
    height: 50px;
    border: 0;
    padding: 12px 24px;
    color: $color-black-2;
    background-color: $color-gray;
    font-family: $font;
    font-size: 18px;
    font-weight: 500;
    background-image: url("data:image/svg+xml;base64,");
    background-position: calc(100% - 16px) 50%;
    background-size: 15px;
    background-repeat: no-repeat;

    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: '';

    &::-ms-expand {
        display: none;
    }
}

.select-emptyorange {
    color: $color-purple;
    background-color: transparent;
    min-width: auto;
    height: auto;
    padding: 0;
    padding-right: 35px;
    background-image: url("data:image/svg+xml;base64,");
}

.sortby {
    display: flex;
    align-items: center;

    span {
        font-size: 18px;
        font-weight: 500;
    }

}

.checkbox {
    display: flex;
    align-items: center;
    // flex-wrap: wrap;

    input {
        display: none;
    }

    label {
        position: relative;
        display: flex;
        align-items: center;
        font-size: 14px;
        font-weight: 500;
        margin-bottom: 0;
        cursor: pointer;

        &::before {
            content: "";
            display: block;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            background-color: #fff;
            border: 1px solid $color-black;
            margin-right: 15px;
        }
    }

    input:checked+label::before {
        background-color: $color-orange;
        border-color: $color-orange;
    }
}